.mainContainer {
  border-radius: 14px;
  border: 1px solid #dee4e7;
  background: #fff;
  width: 100%;
  min-height: 873px;
  display: flex;
  padding: 34px;
  text-align: left;
}

.navHeader {
  font-family: "Gilroy-semibold";
  color: #000;
  font-size: 20px;
}
.navBtnContainer {
  border-radius: 5px;
  width: 326px;
  height: 42px;
  display: flex;
  align-items: center;
  /* только горизонтальные отступы – чтобы не было лишней «наполненности» по высоте */
  padding: 0 14px;
  margin-top: 5px;
  box-sizing: border-box;
  cursor: pointer;
}
.navBtnText {
  flex: 1 1 auto; /* позволяет ужиматься */
  min-width: 0; /* ключ к тому, чтобы flex-item не вылезал за границы */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
  font-size: 18px;
  font-family: "Gilroy-medium";
}
.contentCol {
  /* background-color: red; */
  width: 100%;
}
.contentHeader {
  color: #000;
  font-size: 18px;
  font-family: "Gilroy-medium";
  margin-top: 20px;
}
.contentItem {
  border-radius: 8px;
  border: 1px solid #e9e9e9;
  background: #f5f5f5;
  /* width: 908px; */
  padding: 11px 15px;
  color: #4f4f4f;
  font-size: 16px;
  margin-top: 18px;
  flex: 1;
}

.minorHeader {
  /* color: #f8f8f8; */
  /* font-size: 29px; */
  font-size: clamp(25px, 5vw, 28px);
}
